<template>
    <div>
         <h3>РЕГИСТРАЦИЯ {{jwt}}</h3>
            <b-form style="width: 20em;padding: 0.4em;  margin: auto" class="border rounded shadow-lg p-3 mb-5 bg-white rounded">
              <b-form-group style="text-align: left"  id="input-group-2" label="Логин:" label-for="input-2">
                <b-form-input
                  v-model="username"
                  id="input-2"
                  placeholder="Login"
                ></b-form-input>
              </b-form-group>
              <b-form-group style="text-align: left"  id="input-group-2" label="e-mail" label-for="input-2">
                <b-form-input
                  v-model="email"
                  id="input-2"
                  placeholder="Login"
                ></b-form-input>
              </b-form-group>
              <b-form-group style="text-align: left" id="input-group-3" label="Пароль:" label-for="input-3">
                <b-form-input
                  v-model="password"
                  id="input-3"
                  type="password"
                  placeholder="Password"
                ></b-form-input>
                <b-form-invalid-feedback >
                </b-form-invalid-feedback>
              </b-form-group>
                <b-form-invalid-feedback :state="validation">
                {{validation_message}}
                </b-form-invalid-feedback>
              <b-button  @click="createUser" class="m-1" variant="success">Создать </b-button>
            </b-form>
            <b-form-group >
                <router-link to="/login" style="width: 18em;padding: 0.3em;  margin: auto;" block variant="link">Вход <b-icon icon="person-fill"   aria-hidden="true"></b-icon>  <font-awesome-icon icon="ambulance" /> </router-link>
            </b-form-group>
    </div>
</template>

<script>
    export default {
        name: "Registration",
        data: function () {
            return {
              email:'',
              username: '',
              password: '',
              validation: true,
              validation_message: '',
              jwt:"",
            }
          },
          methods: {
            createUser: function () {
              this.validation = true
              this.axios.post('/api/auth/users/', {username: this.username, password: this.password, email: this.email}).then((response) => {
                  this.$router.push({'name':'Login'})
                  this.jwt = response.data
              }).catch((err) => {
                this.validation_message = err
                this.validation = false
                setTimeout(() =>{ this.validation = true;}, 5000);
              })
            },
          },
    }
</script>

